import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController, LoadingController, MenuController, AlertController } from 'ionic-angular';

import { Item } from '../../models/item';
import { Items } from '../../providers';
import { User } from '../../providers';
import { Api } from '../../providers';


@IonicPage()
@Component({
  selector: 'page-list-master',
  templateUrl: 'list-master.html'
})
export class ListMasterPage {
  currentItems: Item[];
  loaded: boolean;
  itemsshown: any;

  constructor(public navCtrl: NavController, public alertCtrl: AlertController, public items: Items, public api: Api, public menu: MenuController, public modalCtrl: ModalController, public user: User, public loadingCtrl: LoadingController) {
    this.itemsshown = [];
  }



  ionViewCanEnter(){
    let seq = this.api.post('checktoken', []).share();
    seq.subscribe((res: any) => {
      // If the API returned a successful response, mark the user as logged in
      if (res.error !== true) {
        return true;
      } else {
        this.navCtrl.setRoot('LoginPage');
      } 
    }, err => {
      this.navCtrl.setRoot('LoginPage');
    });
  }

  ionViewDidEnter() {
    // the root left menu should be disabled on the tutorial page
    this.menu.enable(true);
  }
  ionViewWillEnter(): Promise<any>{
    let loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: `<img src="assets/svg/bars.svg" width="100%"/>`
    });
    loading.present();
    return new Promise((resolve, reject) => {
      let seq = this.items.query().share();
      seq.subscribe((res: any) => {
          this.currentItems = res;
          resolve(true);
      }, (err) => {
        loading.dismiss();
        resolve(false);
      }, () => { loading.dismiss(); });
    })
  }

  /**
   * Prompt the user to add a new item. This shows our ItemCreatePage in a
   * modal and then adds the new item to our data source if the user created one.
   */
  addItem() {
    let addModal = this.modalCtrl.create('ItemCreatePage');
    addModal.onDidDismiss(item => {
      if (item) {
        this.items.add(item);
      }
    })
    addModal.present();
  }

  /**
   * Delete an item from the list of items.
   */
  deleteItem(item) {
    this.items.delete(item);
  }

  /**
   * Navigate to the detail page for this item.
   */
  openItem(item: Item) {
    this.navCtrl.push('ItemDetailPage', {
      item: item
    });
  }

  loadItems() {
    let loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: `<img src="assets/svg/bars.svg" width="100%"/>`
    });
    loading.present();
    let seq = this.items.query().share();

    seq.subscribe((res: any) => {
      this.currentItems = res;
    }, err => {
      console.log('error get list items');
    }, () => { loading.dismiss(); });
  }
  isItemShown(id: never) {
    if (this.itemsshown.indexOf(id) !== -1) {
      return true;
    }
    return false;
  }
  toggleItem(id: never) {
    console.log('toggle');
    var position = this.itemsshown.indexOf(id);
    if (position !== -1) {
      this.itemsshown.splice(position, 1);
    } else {
      this.itemsshown.push(id);
    }
  }
}



